@import url('https://fonts.googleapis.com/css?family=Roboto');

/* ==============================
GENERAL
============================== */

* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: "sans-serif", Roboto;
}

.wrapper {
    width: 80%;
    max-width: 900px;
    margin: 0 auto;
}

.space {
    width: 100%;
    height: 2em;
    display: inline-block;
}

.center {
    text-align: center;
}

/* ==============================
GRID
============================== */

.proto-grid {
    border: 1px solid red;
}

.proto-grid:after {
    content: "";
    display: table;
    clear: both;
}

.proto-grid .row { width: 100%; }

.proto-grid.spaced-v-s .row { margin-bottom: 0.33em; }
.proto-grid.spaced-v-m .row { margin: 0.66em 0; }
.proto-grid.spaced-v-l .row { margin: 1em 0; }

.proto-grid .row .cell-1,
.proto-grid .row .cell-2,
.proto-grid .row .cell-3,
.proto-grid .row .cell-4,
.proto-grid .row .cell-5,
.proto-grid .row .cell-6,
.proto-grid .row .cell-7,
.proto-grid .row .cell-8,
.proto-grid .row .cell-9,
.proto-grid .row .cell-10,
.proto-grid .row .cell-11,
.proto-grid .row .cell-12 {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.proto-grid .row .cell-1 { width: 8.33%; }
.proto-grid .row .cell-2 { width: 16.66%; }
.proto-grid .row .cell-3 { width: 25%; }
.proto-grid .row .cell-4 { width: 33.33%; }
.proto-grid .row .cell-5 { width: 41.66%; }
.proto-grid .row .cell-6 { width: 50%; }
.proto-grid .row .cell-7 { width: 58.33%; }
.proto-grid .row .cell-8 { width: 66.66%; }
.proto-grid .row .cell-9 { width: 75%; }
.proto-grid .row .cell-10 { width: 83.33%; }
.proto-grid .row .cell-11 { width: 91.66%; }
.proto-grid .row .cell-12 { width: 100%; }

/* ==============================
TABLE
============================== */

.proto-table {
    border: 1px solid #bbb;
    border-collapse: collapse;
}

.proto-table th {
    background-color: #aaaaaa;
    color: black;
    padding: 0.6em 0.8em;
}

.proto-table td {
    color: #666;
    padding: 0.6em 0.8em;
}

.proto-table.filled {
    width: 100%;
}

.proto-table.v-border th,
.proto-table.v-border td {
    border-right: 1px solid #bbb;
    border-left: 1px solid #bbb;
}

.proto-table.h-border th,
.proto-table.h-border td {
    border-top: 1px solid #bbb;
    border-bottom: 1px solid #bbb;
}

.proto-table.striped tr:nth-child(even) td {
    background-color: #e9e9e9;
}

/* ==============================
CARD
============================== */

.card {
    border-radius: 5px;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    display: inline-block;
    text-align: center;
    margin: 0 auto;
    width: 100%;
    vertical-align: middle;
}

.card * {
    border: 2px solid #ccc;
}

/* ==============================
BUTTON
============================== */

input,
select,
button {
    border-radius: 50px;
    padding: 0.5em 2em;
    margin: 1em;
    display: inline-block;
}

